 scss file\r\n*/\r\n\r\n/*******************************\r\n1.General\r\n2.Helper\r\n3.Navbar\r\n4.Hero section\r\n5.Testimonial\r\n6.Pricing\r\n7.Subscribe\r\n8.Footer\r\n9.Responsive\r\n********************************/\r\n\r\n/* Importing Bootstrap SCSS file. */\r\n@import \"~bootstrap/scss/bootstrap\";\r\n@import \"~ngx-owl-carousel-o/lib/styles/scss/owl.carousel\";\r\n@import \"~ngx-owl-carousel-o/lib/styles/scss/owl.theme.default\";\r\n\r\n@import \"variables\";\r\n\r\n/*******************\r\n    1.General\r\n*******************/\r\n\r\nbody {\r\n  font-size: $font-size-base;\r\n  color: $body-color;\r\n  font-family: $font-family-primary;\r\n}\r\n\r\nh1,\r\nh2,\r\nh3,\r\nh4,\r\nh5,\r\nh6 {\r\n  color: $dark;\r\n  font-family: $font-family-secondary;\r\n  font-weight: $font-weight-medium;\r\n}\r\n\r\na {\r\n  text-decoration: none !important;\r\n  outline: none;\r\n}\r\n\r\np {\r\n  line-height: 1.8;\r\n  font-size: 15px;\r\n}\r\n\r\n/*******************\r\n    2.Helper\r\n*******************/\r\n\r\n.card {\r\n  border: none;\r\n  box-shadow: $box-shadow;\r\n  border-radius: 7px;\r\n  margin-bottom: 30px;\r\n}\r\n\r\n.section {\r\n  position: relative;\r\n  padding-top: 80px;\r\n  padding-bottom: 80px;\r\n}\r\n\r\n.vertical-content {\r\n  display: -webkit-flex;\r\n  display: -moz-flex;\r\n  display: -ms-flexbox;\r\n  display: flex;\r\n  align-items: center;\r\n  -webkit-align-items: center;\r\n  justify-content: center;\r\n  -webkit-justify-content: center;\r\n  flex-direction: row;\r\n  -webkit-flex-direction: row;\r\n}\r\n\r\n.bg-overlay {\r\n  background-color: rgba(35, 37, 47, 0.7);\r\n  position: absolute;\r\n  top: 0;\r\n  right: 0;\r\n  left: 0;\r\n  bottom: 0;\r\n  width: 100%;\r\n  height: 100%;\r\n}\r\n\r\n.small-title {\r\n  letter-spacing: 1px;\r\n  font-size: 14px;\r\n}\r\n\r\n.btn {\r\n  font-size: 0.9rem;\r\n  padding: 0.7rem 1.4rem;\r\n  font-weight: $font-weight-medium;\r\n  transition: all 0.4s;\r\n  border-radius: 7px;\r\n\r\n  &:hover {\r\n    transform: translateY(-4px);\r\n    outline: none;\r\n    text-decoration: none;\r\n  }\r\n}\r\n\r\n.btn-sm {\r\n  padding: 0.25rem 0.5rem;\r\n  font-size: 0.875rem;\r\n}\r\n\r\n.btn-rounded {\r\n  border-radius: 30px;\r\n}\r\n\r\n@each $name, $value in $colors {\r\n  .bg-#{$name} {\r\n    background-color: #{$value} !important;\r\n  }\r\n\r\n  .bg-soft-#{$name} {\r\n    background-color: rgba(($value), 0.1) !important;\r\n  }\r\n\r\n  .text-#{$name} {\r\n    color: #{$value} !important;\r\n  }\r\n\r\n  // Icons\r\n  .icon-dual-#{$name} {\r\n    svg {\r\n      color: $value;\r\n      fill: rgba($value, 0.2);\r\n    }\r\n  }\r\n\r\n  .btn-#{$name} {\r\n    color: $white;\r\n    background: #{$value};\r\n    border-color: #{$value};\r\n    box-shadow: 0 8px 14px rgba($value, 0.2);\r\n\r\n    &:hover,\r\n    &:focus,\r\n    &:active,\r\n    &.active,\r\n    &.focus,\r\n    &:not(:disabled):not(.disabled):active,\r\n    &:not(:disabled):not(.disabled):active:focus,\r\n    .open > .dropdown-toggle.btn-primary {\r\n      box-shadow: 0px 16px 31px -16px #{$value};\r\n\r\n      background: darken($value, 4%);\r\n      border-color: darken($value, 4%);\r\n    }\r\n  }\r\n\r\n  .btn-outline-#{$name} {\r\n    color: #{$value};\r\n    border-color: #{$value};\r\n\r\n    &:hover,\r\n    &:focus,\r\n    &:active,\r\n    &.active,\r\n    &.focus,\r\n    &:not(:disabled):not(.disabled):active,\r\n    &:not(:disabled):not(.disabled):active:focus,\r\n    .open > .dropdown-toggle.btn-primary {\r\n      box-shadow: 0px 16px 31px -16px #{$value};\r\n      background: darken($value, 4%);\r\n      border-color: darken($value, 4%);\r\n    }\r\n  }\r\n}\r\n\r\n.font-14 {\r\n  font-size: 14px;\r\n}\r\n\r\n.font-16 {\r\n  font-size: 16px;\r\n}\r\n\r\n.font-18 {\r\n  font-size: 18px;\r\n}\r\n\r\n.font-20 {\r\n  font-size: 20px;\r\n}\r\n\r\n.font-22 {\r\n  font-size: 22px;\r\n}\r\n\r\n.font-24 {\r\n  font-size: 24px;\r\n}\r\n\r\n.icons-sm {\r\n  svg {\r\n    height: 16px;\r\n    width: 16px;\r\n  }\r\n}\r\n\r\n.icons-md {\r\n  height: 32px;\r\n  width: 32px;\r\n}\r\n\r\n.icons-lg {\r\n  svg {\r\n    height: 40px;\r\n    width: 40px;\r\n  }\r\n}\r\n\r\n.box-shadow {\r\n  box-shadow: $box-shadow;\r\n}\r\n\r\n.avatar-md {\r\n  height: 3.5rem;\r\n  width: 3.5rem;\r\n}\r\n\r\n.avatar-title {\r\n  align-items: center;\r\n  background-color: $primary;\r\n  color: $white;\r\n  display: flex;\r\n  font-weight: $font-weight-medium;\r\n  height: 100%;\r\n  justify-content: center;\r\n  width: 100%;\r\n}\r\n\r\n/******************\r\n    3.Navbar\r\n*******************/\r\n\r\n.navbar-custom {\r\n  padding: 20px 0px;\r\n  width: 100%;\r\n  z-index: 999;\r\n  margin-bottom: 0px;\r\n  transition: all 0.5s ease-in-out;\r\n\r\n  .navbar-nav {\r\n    .nav-item {\r\n      .nav-link {\r\n        line-height: 26px;\r\n        color: rgba($dark, 0.8);\r\n        transition: all 0.4s;\r\n        background-color: transparent !important;\r\n        padding: 6px 10px;\r\n        margin: 0 7px;\r\n\r\n        &:hover,\r\n        &:active {\r\n          color: $primary;\r\n        }\r\n      }\r\n\r\n      &.active {\r\n        .nav-link {\r\n          color: $primary;\r\n        }\r\n      }\r\n    }\r\n  }\r\n\r\n  .navbar-btn {\r\n    margin-left: 10px;\r\n    color: $success;\r\n    background: rgba($success, 0.2);\r\n    border-color: transparent;\r\n    box-shadow: none;\r\n\r\n    &:hover {\r\n      color: $white;\r\n      box-shadow: 0px 16px 31px -16px $success;\r\n      background: darken($success, 4%);\r\n      border-color: darken($success, 4%);\r\n    }\r\n  }\r\n}\r\n\r\n.navbar-toggler {\r\n  font-size: 24px;\r\n  margin-top: 5px;\r\n  margin-bottom: 0px;\r\n  color: $white;\r\n}\r\n\r\n/*--- navbar Light ---*/\r\n\r\n.navbar-light {\r\n  .navbar-nav {\r\n    .nav-item {\r\n      .nav-link {\r\n        color: rgba($white, 0.5);\r\n\r\n        &:hover,\r\n        &:active {\r\n          color: $white;\r\n        }\r\n      }\r\n\r\n      &.active {\r\n        .nav-link {\r\n          color: $white;\r\n        }\r\n      }\r\n    }\r\n  }\r\n\r\n  .logo {\r\n    .logo-light {\r\n      display: inline-block;\r\n    }\r\n\r\n    .logo-dark {\r\n      display: none;\r\n    }\r\n  }\r\n\r\n  .nav-sticky {\r\n    .logo {\r\n      .logo-dark {\r\n        display: inline-block;\r\n      }\r\n\r\n      .logo-light {\r\n        display: none;\r\n      }\r\n    }\r\n  }\r\n}\r\n\r\n/*--- navbar sticky ---*/\r\n\r\n.nav-sticky {\r\n  &.navbar-custom {\r\n    margin-top: 0px;\r\n    padding: 10px 0px;\r\n    background-color: $white;\r\n    box-shadow: 0 10px 33px -14px rgba(0, 0, 0, 0.1);\r\n\r\n    .logo {\r\n      .logo-light {\r\n        display: none;\r\n      }\r\n\r\n      .logo-dark {\r\n        display: inline-block;\r\n      }\r\n    }\r\n\r\n    .navbar-nav {\r\n      .nav-item {\r\n        .nav-link {\r\n          color: rgba($dark, 0.9);\r\n\r\n          &:hover,\r\n          &:active {\r\n            color: $primary;\r\n          }\r\n        }\r\n\r\n        &.active {\r\n          .nav-link {\r\n            color: $primary;\r\n          }\r\n        }\r\n      }\r\n    }\r\n  }\r\n}\r\n\r\n/******************\r\n    4.Hero section\r\n*******************/\r\n\r\n.hero-section {\r\n  padding-top: 275px;\r\n  overflow: hidden;\r\n}\r\n\r\n.home-img {\r\n  position: relative;\r\n\r\n  &::before {\r\n    content: \"\";\r\n    position: absolute;\r\n    width: 360px;\r\n    height: 330px;\r\n    background-color: rgba($primary, 0.1);\r\n    border-radius: 30% 70% 59% 41% / 30% 38% 62% 70%;\r\n    z-index: -1;\r\n    top: 90px;\r\n    right: 15px;\r\n  }\r\n}\r\n\r\n/*--- Hero 2 ---*/\r\n\r\n.hero-section-2 {\r\n  padding: 230px 0px 170px 0px;\r\n}\r\n\r\n.registration-form {\r\n  .form-control {\r\n    box-shadow: none;\r\n\r\n    &:focus {\r\n      border-color: $primary;\r\n    }\r\n  }\r\n}\r\n\r\n/*--- Hero 5 ---*/\r\n\r\n.hero-section-5 {\r\n  padding: 300px 0px 220px 0px;\r\n}\r\n.dropdown-language {\r\n  color: #f60f4f !important;\r\n}\r\n\r\n.play-icon-circle {\r\n  height: 50px;\r\n  width: 50px;\r\n  background-color: $success;\r\n  line-height: 50px;\r\n  text-align: center;\r\n  border-radius: 50%;\r\n  display: inline-block;\r\n  margin-right: 5px;\r\n}\r\n\r\n.play-iconbar {\r\n  color: $white;\r\n  font-size: 17px;\r\n}\r\n\r\n/******************\r\n    5.Testimonial\r\n*******************/\r\n\r\n.testi-carousel {\r\n  .owl-nav {\r\n    position: absolute;\r\n    top: -67px;\r\n    right: 0;\r\n    margin-top: 0px;\r\n\r\n    .owl-prev,\r\n    .owl-next {\r\n      display: inline-block;\r\n      width: 32px;\r\n      height: 32px;\r\n      line-height: 27px;\r\n      background: rgba($primary, 0.2);\r\n      color: $primary;\r\n      border-radius: 50%;\r\n      font-size: 20px;\r\n    }\r\n  }\r\n\r\n  .owl-theme .owl-nav [class*=\"owl-\"]:hover {\r\n    background: $primary;\r\n    color: $white;\r\n  }\r\n\r\n  .owl-item {\r\n    padding: 0px 10px;\r\n  }\r\n\r\n  .owl-dots {\r\n    .owl-dot {\r\n      &:focus {\r\n        outline: 0;\r\n      }\r\n\r\n      span {\r\n        background: rgba($primary, 0.2);\r\n        border: 2px solid transparent;\r\n      }\r\n\r\n      &.active,\r\n      &:hover {\r\n        span {\r\n          background: $white;\r\n          border: 2px solid $primary;\r\n        }\r\n      }\r\n    }\r\n  }\r\n}\r\n\r\n/********** Clients ***********/\r\n\r\n.client-images {\r\n  img {\r\n    max-height: 94px;\r\n    width: auto !important;\r\n    margin: 10px auto;\r\n    opacity: 0.6;\r\n    transition: all 0.5s;\r\n\r\n    &:hover {\r\n      opacity: 0.9;\r\n    }\r\n  }\r\n}\r\n\r\n/******************\r\n    6.Pricing\r\n*******************/\r\n\r\n.pricing-nav-tabs {\r\n  display: inline-block;\r\n  background-color: $white;\r\n  box-shadow: $box-shadow;\r\n  padding: 5px;\r\n  border-radius: 7px;\r\n\r\n  li {\r\n    display: inline-block;\r\n\r\n    .nav-link {\r\n      border-radius: 7px;\r\n      color: $body-color;\r\n      padding: 0.6rem 1.4rem;\r\n\r\n      &.active {\r\n        background-color: $primary;\r\n      }\r\n    }\r\n  }\r\n}\r\n\r\n.pricing-plan {\r\n  max-width: 415px;\r\n  margin: 0px auto;\r\n  margin-top: 30px;\r\n}\r\n\r\n/******************\r\n    7.Subscribe\r\n*******************/\r\n\r\n.subscribe {\r\n  .form-control {\r\n    height: 46px;\r\n    border-radius: 7px;\r\n    font-size: 15px;\r\n    box-shadow: none;\r\n\r\n    &:focus {\r\n      border-color: $primary;\r\n    }\r\n  }\r\n}\r\n\r\n/******************\r\n    8.Footer\r\n*******************/\r\n\r\n.footer {\r\n  background-color: $footer-bg;\r\n  color: rgba($white, 0.5);\r\n  padding-top: 80px;\r\n  padding-bottom: 40px;\r\n}\r\n\r\n.footer-list-title {\r\n  font-size: 16px;\r\n  color: $white;\r\n}\r\n\r\n.footer-list-menu {\r\n  li {\r\n    a {\r\n      display: inline-block;\r\n      font-size: 15px;\r\n      color: rgba($white, 0.5);\r\n      margin-bottom: 14px;\r\n      transition: all 0.4s;\r\n\r\n      &:hover {\r\n        color: rgba($white, 0.9);\r\n      }\r\n    }\r\n  }\r\n}\r\n\r\n/******************\r\n    9.Responsive\r\n*******************/\r\n\r\n@media (max-width: 991px) {\r\n  .navbar-custom {\r\n    margin-top: 0px;\r\n    padding: 10px 0px !important;\r\n    background-color: $light !important;\r\n    box-shadow: 0 10px 33px -14px rgba(0, 0, 0, 0.1);\r\n\r\n    &.navbar-expand-lg > .container {\r\n      width: 90%;\r\n    }\r\n\r\n    .logo {\r\n      img {\r\n        height: 18px;\r\n      }\r\n    }\r\n\r\n    .navbar-nav {\r\n      .nav-item {\r\n        .nav-link {\r\n          transition: all 0.4s;\r\n          margin: 0px;\r\n          padding: 6px 0px;\r\n          color: rgba($dark, 0.9);\r\n        }\r\n\r\n        &.active {\r\n          .nav-link {\r\n            color: $primary;\r\n          }\r\n        }\r\n      }\r\n    }\r\n\r\n    .navbar-btn {\r\n      margin-left: 0px;\r\n    }\r\n  }\r\n\r\n  .navbar-toggler {\r\n    font-size: 24px;\r\n    margin-top: 0px;\r\n    margin-bottom: 0px;\r\n    color: rgba($dark, 0.9);\r\n  }\r\n\r\n  .hero-section {\r\n    padding-top: 130px;\r\n  }\r\n}\r\n\r\n@media (max-width: 767.98px) {\r\n  .hero-title {\r\n    font-size: 32px;\r\n  }\r\n}\r\n\r\n@media (max-width: 575.98px) {\r\n  .testi-carousel {\r\n    .owl-nav {\r\n      display: none;\r\n    }\r\n  }\r\n}\r\n","/*!\n * Bootstrap v4.5.0 (https://getbootstrap.com/)\n * Copyright 2011-2020 The Bootstrap Authors\n * Copyright 2011-2020 Twitter, Inc.\n * Licensed under MIT (https://github.com/twbs/bootstrap/blob/master/LICENSE)\n */\n\n@import \"functions\";\n@import \"variables\";\n@import \"mixins\";\n@import \"root\";\n@import \"reboot\";\n@import \"type\";\n@import \"images\";\n@import \"code\";\n@import \"grid\";\n@import \"tables\";\n@import \"forms\";\n@import \"buttons\";\n@import \"transitions\";\n@import \"dropdown\";\n@import \"button-group\";\n@import \"input-group\";\n@import \"custom-forms\";\n@import \"nav\";\n@import \"navbar\";\n@import \"card\";\n@import \"breadcrumb\";\n@import \"pagination\";\n@import \"badge\";\n@import \"jumbotron\";\n@import \"alert\";\n@import \"progress\";\n@import \"media\";\n@import \"list-group\";\n@import \"close\";\n@import \"toasts\";\n@import \"modal\";\n@import \"tooltip\";\n@import \"popover\";\n@import \"carousel\";\n@import \"spinners\";\n@import \"utilities\";\n@import \"print\";\n","// Do not forget to update getting-started/theming.md!\n:root {\n  // Custom variable values only support SassScript inside `#{}`.\n  @each $color, $value in $colors {\n    --#{$color}: #{$value};\n  }\n\n  @each $color, $value in $theme-colors {\n    --#{$color}: #{$value};\n  }\n\n  @each $bp, $value in $grid-breakpoints {\n    --breakpoint-#{$bp}: #{$value};\n  }\n\n  // Use `inspect` for lists so that quoted items keep the quotes.\n  // See https://github.com/sass/sass/issues/2383#issuecomment-336349172\n  --font-family-sans-serif: #{inspect($font-family-sans-serif)};\n  --font-family-monospace: #{inspect($font-family-monospace)};\n}\n","// stylelint-disable at-rule-no-vendor-prefix, declaration-no-important, selector-no-qualifying-type, property-no-vendor-prefix\n\n// Reboot\n//\n// Normalization of HTML elements, manually forked from Normalize.css to remove\n// styles targeting irrelevant browsers while applying new styles.\n//\n// Normalize is licensed MIT. https://github.com/necolas/normalize.css\n\n\n// Document\n//\n// 1. Change from `box-sizing: content-box` so that `width` is not affected by `padding` or `border`.\n// 2. Change the default font family in all browsers.\n// 3. Correct the line height in all browsers.\n// 4. Prevent adjustments of font size after orientation changes in IE on Windows Phone and in iOS.\n// 5. Change the default tap highlight to be completely transparent in iOS.\n\n*,\n*::before,\n*::after {\n  box-sizing: border-box; // 1\n}\n\nhtml {\n  font-family: sans-serif; // 2\n  line-height: 1.15; // 3\n  -webkit-text-size-adjust: 100%; // 4\n  -webkit-tap-highlight-color: rgba($black, 0); // 5\n}\n\n// Shim for \"new\" HTML5 structural elements to display correctly (IE10, older browsers)\n// TODO: remove in v5\n// stylelint-disable-next-line selector-list-comma-newline-after\narticle, aside, figcaption, figure, footer, header, hgroup, main, nav, section {\n  display: block;\n}\n\n// Body\n//\n// 1. Remove the margin in all browsers.\n// 2. As a best practice, apply a default `background-color`.\n// 3. Set an explicit initial text-align value so that we can later use\n//    the `inherit` value on things like `<th>` elements.\n\nbody {\n  margin: 0; // 1\n  font-family: $font-family-base;\n  @include font-size($font-size-base);\n  font-weight: $font-weight-base;\n  line-height: $line-height-base;\n  color: $body-color;\n  text-align: left; // 3\n  background-color: $body-bg; // 2\n}\n\n// Future-proof rule: in browsers that support :focus-visible, suppress the focus outline\n// on elements that programmatically receive focus but wouldn